  border: 1px solid #ccc; }

/*アコーディオンタイトル*/
.title {
  position: relative;
  /*+マークの位置基準とするためrelative指定*/
  cursor: pointer;
  font-size: 14px;
  transition: all 0.5s ease;
  background-color: #ebc9d1;
  margin-bottom: 0;
  border-radius: 30px;
  padding: 2px 0;
  margin-top: 25px;
  z-index: 2;
  height: 35px; }
  .title > img {
    margin-right: 10px;
    width: 34px; }

/*アイコンの＋と×*/
.title::before,
.title::after {
  position: absolute;
  content: "";
  width: 15px;
  height: 2px;
  background-color: #c2617c; }

.title::before {
  top: 48%;
  right: 2%;
  transform: rotate(0deg); }

.title::after {
  top: 48%;
  right: 2%;
  transform: rotate(90deg); }

/*　closeというクラスがついたら形状変化　*/
.title.close::before {
  transform: rotate(45deg); }

.title.close::after {
  transform: rotate(-45deg); }

/*アコーディオンで現れるエリア*/
.box {
  display: none;
  /*はじめは非表示*/
  background: #eeeeef;
  padding: 6px 20px 10px 20px;
  position: relative;
